        setInter(){
         this.time =  setInterval(()=>{
           if((this.newListWidth-this.left)>968){
             this.left +=1
           }else{
             this.left =0
           }
          },50);
        },
        enter(){
          clearInterval(this.time)
        },
        leave(){
          this.setInter()
        },
