        if(val===31){
            this.$router.push('/selected');
            this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选龙头']);
            this.$store.dispatch('listenTOsolutionNav',val)
          }else if(val===32){
            this.$router.push('/selected');
            this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选五金']);
            this.$store.dispatch('listenTOsolutionNav',val)
