            this.$store.dispatch('listenTOsolutionNav',val)
          }
          $('body,html').animate({
            scrollTop:0
          },500)
        },
        clickNewPro(){
          let height = document.getElementById('newPro').offsetTop;
          $('body,html').animate({
            scrollTop:height
          },1000)
        },
        clickCustomized(){
          let height = document.getElementById('customized').offsetTop;
