<template>
  <div class="title">
    <div class="line"></div>
    <div class="text">{{title}}</div>
    <div class="line"></div>
  </div>
</template>

<script>
    export default {
      name: "titleLine",
      props:['title']
    }
</script>

<style scoped>
  .title{
    display: flex;
    width: 100%;
    margin: 0 auto;
  }
  .line{
    flex: 1;
    position: relative;
    top:-8px;
    border-bottom: 1px solid #cccccc;
  }

  .text {
    padding: 0 12px;
    font-size: 16px;
    font-weight: 700;
    color: #333;
  }
  @media screen and (max-width: 768px){
    .text{
      padding: 0 12px;
      font-size: 14px;
      font-weight: 700;
      color: #333;
    }
    .line{
      flex: 1;
      position: relative;
      top:-7px;
      border-bottom: 1px solid #cccccc;
    }
  }
</style>



